import api from '../../api';
export default {
		data() {
			return {
				navArr:[
				 		   {id:1,name:`提问`},
				 		   {id:2,name:`回答`},
				 		   {id:3,name:`收藏`},
				 		   {id:4,name:`消息`}
				],
				navdefault:1,
				page:1,
				addmores:'加载更多',
				content:[]
			}
		},
		onLoad() {
			this.requestFun(this.navdefault,this.page);
		},
		onShareAppMessage(res) {
		    if (res.from === 'button') {// 来自页面内分享按钮
		      console.log(res.target)
		    }
		    return {
		      title: '自定义分享标题',
		      path: '/pages/test/test?id=123'
		    }
		},
		methods: {
			// 导航栏
			navtabfn(id){
				let _that  = this;
				console.log(id)
				this.navdefault = id;
				if(id < 4){
					api.getmyAsk({
						token:'jcm',
						page:1
					},id).then(res=>{
						console.log('res',res)
						if(res.code==1){
							_that.content = res.data
						}
					}) 
				}else{ 
					_that.content = '';
				}
				_that.addmores = '加载更多';
				
		    },
			// 首次请求数据获||切换请求数据
			requestFun(tabid,page){
				uni.showLoading({title: '数据加载中',mask:true})
				api.getmyAsk({
					token:'jcm',
					page:page
				},tabid).then(res=>{
					console.log('res',res)
					if(res.code==1){
						uni.hideLoading();
						this.content = res.data;
					}
				})  
			},
			
			// 加载更多
			addmore(tabid){
				   let _that = this;
				   this.page++;
				   if(_that.addmores=='已经到低咯') return false;
				   uni.showLoading({title: '数据加载中',mask:true})
				   api.getmyAsk({
				   	token:'jcm',
				   	page:_that.page
				   },tabid).then(res=>{
				   	if(res.code==1){
						uni.hideLoading();
						if(res.data[0]){
							if(_that.page>1){
								_that.content.push.apply(_that.content,res.data);
							}else{
								_that.content = res.data;
							}
						}else{
							_that.addmores='已经到低咯'
						}
				   	}
				   })
			},
			
			// 删除
			delfn(id,index){
				   let _that = this;
				   // _that.content.splice(index,1);
				   api.delask({
					   token:'jcm',	
					   id:id
				   }).then(res=>{
					   if(res.code==1) {
						   _that.content.splice(index,1);
					   }
				   })
				   
			},
			
			//[====分享]
			share(){
				uni.share({
				    provider: "weixin",
				    scene: "WXSceneSession",
				    type: 0,
				    href: "http://uniapp.dcloud.io/",
				    title: "uni-app分享",
				    summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
				    imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
				    success: function (res) {
				        console.log("success:" + JSON.stringify(res));
				    },
				    fail: function (err) {
				        console.log("fail:" + JSON.stringify(err));
				    }
				});
			}
	

			
		}
}
